* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  list-style: none;
  font-weight: 300;
  font-size: 30px;
}
.country-titles {
  font-weight: 300;
  font-size: 30px;
}
.country-info {
  font-weight: 300;
}
.country-descr {
  font-weight: 300;
  margin-bottom: 0;
  margin-top: 10px;
}
